@font-face {
    font-family: 'SEGA LOGO FONT';
    font-style: normal;
    font-weight: normal;
    src: local('SEGA LOGO FONT'), url('SEGA.woff') format('woff');
    }

* {
    padding: 0px;
    margin: 0px;
    font-family: -apple-system,system-ui,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif 
}

body {
    background: linear-gradient(to left, rgba(7,27,82,1), rgba(0,128,128,1));
}

.cardList {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 5px;
}

.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 220px;
    padding-bottom: 5px;
    margin-left: 6px;
    margin-top: 6px;
    background: linear-gradient(to right, rgb(144, 238, 183), rgb(116, 192, 125));
    border-radius: 5px;
}

.container > img {
    width: 200px;
    height: 200px;
}

.container:hover {
    transform: scale(1.04);
    transition-duration: 200ms;
    cursor: pointer;
}

.information {
    text-align: center;
}

.information > h2 {
    font-size: 1.2rem;
}

.header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap:20px;
}

.header > h1 {
    padding-top: 15px;
    font-family: 'SEGA LOGO FONT', sans-serif;
    font-weight: 200;
    font-size: 2.5rem;
    color: #0ccac4;
}

.header > input {
    text-indent: 1px;
    background-color: lightcyan;
    border: none;
    padding: 6px 10px;
    margin-bottom: 4px;
}

input:focus {
    border: 1px solid rgb(122, 159, 159);
    outline: none;
}

